import { randomColor } from '../util/util.js'
import TreeNode from '../components/TreeNode.js'

class Tree{
    constructor(data, cw, ch, ctx) {
      this.oldData = JSON.parse(JSON.stringify(data))
      this.data = this.transData(this.oldData)
      this.drawNode(this.data)
    }

    transData (data, startPos) {
      let newData = [];
      for(let [i, v] of data.entries()) {
        let nowData = {data: {}};
        nowData.data.text = v.text;
        nowData.options = {
          currentPos: this.getPos(i, v.level),
          border: {
            width: 1,
            color: '#ff0000'
          },
          background: {
            color: randomColor()
          },
          radius: 20,
          startPos: startPos?startPos:{}
        }
        if (v.children) {
          nowData.children = this.transData(v.children, nowData.options.currentPos, data.length)
        }
        newData.push(nowData)
      }
      return newData;
    }

    getPos(i, level) {
      if (level === 0) {
        return {x: this.cw/2, y: this.ch/2}
      }
      return {x: this.cw/2 - Math.cos(Math.PI / 6 * i) * 100, y: this.ch/2 - Math.sin(Math.PI / 6 * i) * 100}
    }

    getY(level, len, i) {
      let num; // 获取父节点的个数
      if (level === 0) {
        num = 0;
      } else {
        num = this.getLevelNum(level - 1)
      }
      let y = ch / (num + 1) / (len + 1) * ++i;
      console.log(y)
      return y
    }

    drawNode(arr) {
      for(let [i ,v] of arr.entries()) {
        let node = new TreeNode(v.options, this.ctx)
        if (v.children) {
          this.drawNode(v.children)
        }
      }
    }

    getLevelNum(level) {
      let num = 0

      let arr = this.getLevelArr(level, this.oldData)
      for(let i of tileArray(arr)) {
        num += parseInt(i)
      }
      return num
    }

    getLevelArr(level, arr) {
      if (Array.isArray(arr)) {
        let levelArr = []
        if (level == 0) {
          return levelArr.push(arr.length)
        }
        if (arr.length) {
          for(let i of arr) {
            if (i.level + 1 == level) {
              levelArr.push(i.children.length)
            } else {
              levelArr.push(this.getLevelArr(level, i.children))
            }
          }
        } else {
          levelArr.push(1)
        }
        return levelArr
      }
    }

    refresh() {
      this.drawNode(this.data)
    }
  }

  export default Tree