// 展平数组
export const tileArray = (arr) => {
    return arr.toString().split(',')
}

// 随机颜色
export const randomColor =  () => {
    return '#' + (parseInt(Math.random() * 265)).toString(16) + (parseInt(Math.random() * 265)).toString(16) + (parseInt(Math.random() * 265)).toString(16)
  }

// 获取缩放中心点
export const getOrigin = (first, second) => {
    return {
        x: (first.x + second.x) / 2,
        y: (first.y + second.y) / 2
    };
}

// 获取缩放距离
export const getDistance = (start, stop) => {
    return Math.sqrt(Math.pow((stop.x - start.x), 2) + Math.pow((stop.y - start.y), 2)) 
}

// 获取缩放比例
