<template>
  <div id="app">
    <div class="show">{{showText}}</div>
    <canvas id="canvas"></canvas>
  </div>
</template>

<script>
import img from './assets/3.jpg'
import data from './data/data.js'
import { tileArray, getOrigin, getDistance, getScale } from './util/util.js'
import TreeNode from './components/TreeNode.js'
import Tree from './components/Tree.js'

export default {
  name: 'app',
  data: function () {
    return {
      showText: ''
    }
  },
  mounted() {
    let _this = this;
    let canvas = document.getElementById('canvas')
    let ctx = canvas.getContext('2d')
    let app = document.getElementById('app')
    const cw = canvas.width = window.getComputedStyle(app).width.replace('px', '')
    const ch = canvas.height =  window.getComputedStyle(app).height.replace('px', '')
    let distance = {}
    let move = {}
    let moveTo = {}
    let origin
    let scale = 1
    Tree.prototype.cw = cw
    Tree.prototype.ch = ch
    Tree.prototype.ctx = ctx

    // 阻止滚动
    document.body.addEventListener('touchmove', function(event){
      // 判断默认行为是否可以被禁用
      // if (event.cancelable) {
        // 判断默认行为是否已经被禁用
        // if (event.defaultPrevented) {
          event.preventDefault();
        // }
      // }
    })

    canvas.addEventListener('touchstart', handleTouch)
    // canvas.addEventListener('touchmove', handleTouch)
    // canvas.addEventListener('touchend', handleTouch)
    // canvas.addEventListener('touchcancel', handleTouch)

    let tree = new Tree(data)

    // 触摸事件处理方法
    function handleTouch (e) {
      switch(e.type) {
        case 'touchstart':
          if (e.touches.length === 1) {
            move = {
              x: e.touches[0].clientX,
              y: e.touches[0].clientY
            }
          } else if (e.touches.length > 1) {
            distance.start = getDistance({
              x: e.touches[0].clientX,
              y: e.touches[0].clientY
            }, {
              x: e.touches[1].clientX,
              y: e.touches[1].clientY
            })
            origin = getOrigin({
                x: event.touches[0].pageX, 
                y: event.touches[0].pageY
            }, {
                x: event.touches[1].pageX, 
                y: event.touches[1].pageY
            });
          }
          break;
        case 'touchmove':
          if (e.touches.length === 1) {
            moveTo = {
                x: e.touches[0].screenX,
                y: e.touches[0].screenY
            }
            setTransform()
          } else if (e.touches.length === 2) {
            distance.stop = getDistance({
                x: e.touches[0].screenX, 
                y: e.touches[0].screenY  
            }, {
                x: e.touches[1].screenX, 
                y: e.touches[1].screenY
            });
            scale = distance.stop/distance.start
            distance.start = distance.stop
            setScaleAnimation();
          }
          break;
        case 'touchend':
            scale = 1;
            setScaleAnimation(scale);
            break;
        case 'touchcancel':
            scale = 1;
            setScaleAnimation(scale);
            break;
        default:;
      }
    }

    function setScaleAnimation() {
      ctx.save()
      ctx.clearRect(0, 0, cw, ch)
      ctx.scale(scale, scale)
      ctx.translate(-origin.x * (scale - 1), -origin.y * (scale - 1))
      tree.refresh()
    }

    function setTransform() {
      let x = moveTo.x - move.x
      let y = moveTo.y - move.y
      ctx.clearRect(0, 0, cw, ch)
      ctx.translate(x, y)
      tree.refresh()
      move = moveTo
    }
  },
}

</script>

<style lang="scss">
*{
  padding: 0;
  margin: 0;
}
#app {
  width: 100%;
  height: 100vh;
}
</style>
